.education__programs {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.1px;
  grid-area: 2 / 1 / 3 / 2;
}

@media (min-width: 600px) {
  .education__programs {
    grid-area: 1 / 1 / 2 / 2;
    font-size: 1.56vw;
    line-height: 2.6vw;
  }
}

@media (min-width: 1024px) {
  .education__programs {
    font-size: 18px;
    line-height: 30px;
  }
}
