.projects__gallery {
  margin: 0 0 10px;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  /*grid-auto-flow: dense;*/
  max-height: 288px;
  overflow: hidden;
  background-color: var(--blue);
}

@media (min-width: 768px) {
  .projects__gallery {
    margin: 0 0 16px;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    max-height: 450px;
  }
}

@media (min-width: 1440px) {
  .projects__gallery {
    margin: 0 0 24px;
    grid-template-columns: repeat(4, 1fr);
    max-height: 506px;
  }
}
