.team__slider-container {
  width: calc(100% - 32px);
  position: relative;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .team__slider-container {
    width: calc(100%-78px);
  }
}

@media (min-width: 1024px) {
  .team__slider-container {
    max-width: 930px;
  }
}

@media (min-width: 1440px) {
  .team__slider-container {
    max-width: 1110px;
  }
}
